import React, { useState } from 'react';
import './Servicios.css';

const Servicios = () => {
  const [isDownloadEnabled, setIsDownloadEnabled] = useState(false);
  const [countdown, setCountdown] = useState(null); // Estado para la cuenta regresiva

  // URL de la API Gateway para invocar la funcin Lambda
  const apiGatewayUrl = 'https://hih8phdvf9.execute-api.us-east-2.amazonaws.com/prod/invoke-lambda';

  const handleDownload = () => {
    const s3Url = 'https://mi-bucket-de-datos-upch.s3.us-east-2.amazonaws.com/info.csv';
    const link = document.createElement('a');
    link.href = s3Url;
    link.download = 'info.csv';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const handleCreateData = async () => {
    setIsDownloadEnabled(false); // Desactivar el botn de descarga
    setCountdown(3); // Iniciar la cuenta regresiva desde 3

    // Temporizador para la cuenta regresiva
    const intervalId = setInterval(() => {
      setCountdown(prevCountdown => {
        if (prevCountdown === 1) {
          clearInterval(intervalId); // Detener el temporizador cuando llegue a 0
          setIsDownloadEnabled(true); // Habilitar el botn de descarga
          return null; // Ocultar la cuenta regresiva
        }
        return prevCountdown - 1; // Disminuir el contador en 1
      });
    }, 1000);

    // Intentar hacer la solicitud a la API Gateway
    try {
      const response = await fetch(apiGatewayUrl, {
        method: 'POST'
      });

      if (!response.ok) {
        alert('Hubo un error al crear los datos.');
      }
    } catch (error) {
      console.error('Error al invocar la funcin Lambda:', error);
      
    }
  };

  return (
    <div className="servicios-contenedor">
      <h2>Descargar Datos de Sensores</h2>
      <p>Puedes crear y descargar los datos recopilados.</p>
      
      <div className="servicios-formulario">
        <button 
          onClick={handleCreateData} 
          style={{ padding: '10px 20px', marginBottom: '10px' }}
        >
          Crear Datos
        </button>

        <button 
          onClick={handleDownload} 
          disabled={!isDownloadEnabled} 
          style={{ 
            padding: '10px 20px', 
            backgroundColor: isDownloadEnabled ? '#007acc' : '#ccc', 
            color: isDownloadEnabled ? '#fff' : '#666', 
            cursor: isDownloadEnabled ? 'pointer' : 'not-allowed' 
          }}
        >
          Descargar CSV
        </button>

        {/* Mostrar cuenta regresiva si est activa */}
        {countdown !== null && <p>Datos listos en: {countdown}</p>}
      </div>
    </div>
  );
};

export default Servicios;
