import React, { useState, useEffect } from 'react';
import Papa from 'papaparse';
import { BarChart, Bar, LineChart, Line, ScatterChart, Scatter, XAxis, YAxis, CartesianGrid, Tooltip } from 'recharts';

const Datos = () => {
    const [data, setData] = useState([]);
    const [chartType, setChartType] = useState({}); // Estado para almacenar el tipo de grfico para cada parmetro

    useEffect(() => {
        // Cargar y procesar el archivo CSV
        Papa.parse('/info.csv', {
            download: true,
            header: true,
            dynamicTyping: true,
            complete: (result) => {
                const processedData = processMonthlyData(result.data);
                setData(processedData);

                // Inicializar el tipo de grfico como 'bar' para cada parmetro
                const initialChartType = {};
                Object.keys(colorMap).forEach(param => {
                    initialChartType[param] = 'bar';
                });
                setChartType(initialChartType);
            }
        });
    }, []);

    // Funcin para procesar y agrupar los datos por mes, calculando el promedio de cada valor
    const processMonthlyData = (rawData) => {
        const monthlyMap = {};

        rawData.forEach(row => {
            const date = new Date(row.created_at);
            if (isNaN(date.getTime())) return; // Ignorar filas con fechas invlidas

            const monthYear = `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}`;

            if (!monthlyMap[monthYear]) {
                monthlyMap[monthYear] = {
                    temperatura: row.temperatura || 0,
                    humedad: row.humedad || 0,
                    presion: row.presion || 0,
                    radiacion_uv: row.radiacion_uv || 0,
                    ozono: row.ozono || 0,
                    velocidad_viento: row.velocidad_viento || 0,
                    pm25: row.pm25 || 0,
                    co2_aire: row.co2_aire || 0,
                    co2_suelo: row.co2_suelo || 0,
                    metano: row.metano || 0,
                    humedad_suelo: row.humedad_suelo || 0,
                    count: 1
                };
            } else {
                monthlyMap[monthYear].temperatura += row.temperatura || 0;
                monthlyMap[monthYear].humedad += row.humedad || 0;
                monthlyMap[monthYear].presion += row.presion || 0;
                monthlyMap[monthYear].radiacion_uv += row.radiacion_uv || 0;
                monthlyMap[monthYear].ozono += row.ozono || 0;
                monthlyMap[monthYear].velocidad_viento += row.velocidad_viento || 0;
                monthlyMap[monthYear].pm25 += row.pm25 || 0;
                monthlyMap[monthYear].co2_aire += row.co2_aire || 0;
                monthlyMap[monthYear].co2_suelo += row.co2_suelo || 0;
                monthlyMap[monthYear].metano += row.metano || 0;
                monthlyMap[monthYear].humedad_suelo += row.humedad_suelo || 0;
                monthlyMap[monthYear].count += 1;
            }
        });

        const monthlyData = Object.entries(monthlyMap).map(([monthYear, data]) => ({
            created_at: monthYear,
            temperatura: data.temperatura / data.count,
            humedad: data.humedad / data.count,
            presion: data.presion / data.count,
            radiacion_uv: data.radiacion_uv / data.count,
            ozono: data.ozono / data.count,
            velocidad_viento: data.velocidad_viento / data.count,
            pm25: data.pm25 / data.count,
            co2_aire: data.co2_aire / data.count,
            co2_suelo: data.co2_suelo / data.count,
            metano: data.metano / data.count,
            humedad_suelo: data.humedad_suelo / data.count
        }));

        return monthlyData;
    };

    // Colores asignados a cada parmetro
    const colorMap = {
        temperatura: "#8884d8",
        humedad: "#82ca9d",
        presion: "#ffc658",
        radiacion_uv: "#ff7300",
        ozono: "#8884d8",
        velocidad_viento: "#8dd1e1",
        pm25: "#d0ed57",
        co2_aire: "#a4de6c",
        co2_suelo: "#d084bd",
        metano: "#f28e2b",
        humedad_suelo: "#ff9999"
    };

    // Funcin para alternar el tipo de grfico de un parmetro especfico
    const toggleChartType = (parametro) => {
        setChartType(prevType => ({
            ...prevType,
            [parametro]: prevType[parametro] === 'bar' ? 'line' : prevType[parametro] === 'line' ? 'scatter' : 'bar'
        }));
    };

    return (
        <div className="datos-contenedor" style={{ position: 'fixed', top: 0, left: 0, width: '100vw', height: '100vh', backgroundColor: '#f9f9f9', overflowY: 'auto' }}>
            <button className="boton-regresar" onClick={() => window.history.back()} style={{ position: 'absolute', top: 20, left: 20, padding: '10px 20px', backgroundColor: '#007acc', color: '#ffffff', border: 'none', borderRadius: '6px', cursor: 'pointer' }}>
                Regresar
            </button>
            <div style={{ width: '90%', maxWidth: '1200px', margin: '80px auto 20px', textAlign: 'center' }}>
                <h2>Anlisis de Datos de Sensores</h2>

                {/* Histograma, grfico de lneas o grfico de dispersin para cada parmetro */}
                {Object.keys(colorMap).map((parametro, index) => (
                    <div key={index} style={{ width: '90%', maxWidth: '1000px', backgroundColor: '#ffffff', borderRadius: '8px', padding: '20px', margin: '20px auto', boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)' }}>
                        <h3>Grfico de {parametro.charAt(0).toUpperCase() + parametro.slice(1)}</h3>
                        <button onClick={() => toggleChartType(parametro)} style={{ marginBottom: '10px', padding: '5px 10px', border: 'none', backgroundColor: '#007acc', color: '#ffffff', borderRadius: '5px', cursor: 'pointer' }}>
                            Cambiar a {chartType[parametro] === 'bar' ? 'Grfico de Lneas' : chartType[parametro] === 'line' ? 'Grfico de Dispersin' : 'Histograma'}
                        </button>
                        {chartType[parametro] === 'bar' ? (
                            <BarChart width={800} height={300} data={data}>
                                <CartesianGrid strokeDasharray="3 3" />
                                <XAxis dataKey="created_at" />
                                <YAxis />
                                <Tooltip />
                                <Bar dataKey={parametro} fill={colorMap[parametro]} />
                            </BarChart>
                        ) : chartType[parametro] === 'line' ? (
                            <LineChart width={800} height={300} data={data}>
                                <CartesianGrid strokeDasharray="3 3" />
                                <XAxis dataKey="created_at" />
                                <YAxis />
                                <Tooltip />
                                <Line type="monotone" dataKey={parametro} stroke={colorMap[parametro]} />
                            </LineChart>
                        ) : (
                            <ScatterChart width={800} height={300} data={data}>
                                <CartesianGrid strokeDasharray="3 3" />
                                <XAxis dataKey="created_at" />
                                <YAxis />
                                <Tooltip />
                                <Scatter dataKey={parametro} fill={colorMap[parametro]} />
                            </ScatterChart>
                        )}
                    </div>
                ))}
            </div>
        </div>
    );
};

export default Datos;
